import APP from "./app";
import React from "react";
import classnames from "classnames";
import LinkedStateMixin from "react-addons-linked-state-mixin";
import { Glyphicon, Navbar, NavBrand, Nav, NavItem, CollapsibleNav } from "react-bootstrap/lib";

export const Layout = {};

Layout.Global = React.createClass({
	render: function () {
		return (
			<div>
				<AppNavbar />
				<div className="container">
					{this.props.children}
				</div>
			</div>
		);
	}
});

class AppNavbar extends React.Component {
	constructor() {
		super();
	}
	
	onLogoff(event) {
		event.preventDefault();
		APP.logged = false;
		APP.render();
	}
	
	navigateTo(view, event) {
		event.preventDefault();
		APP.render(view);
	}
	
	active(view) {
		return APP.view === view;
	}
	
	render() {
		const showNavigation = APP.role !== "Individu";
	
		return (
			<Navbar toggleNavKey={showNavigation ? 0 : undefined}>
				<NavBrand>
					<a className="navbar-brand" href="#" onClick={this.navigateTo.bind(this, APP.role === "Agent" ? "dashboard" : "login")}>Application Métiers</a>
				</NavBrand>
				{ showNavigation ?
					<CollapsibleNav eventKey={0}>
						<Nav navbar>
							<NavItem eventKey={1} active={this.active("stagiaires")} href="#" onClick={this.navigateTo.bind(this, "stagiaires")}>Stagiaires</NavItem>
							<NavItem eventKey={2} active={this.active("evenements")} href="#" onClick={this.navigateTo.bind(this, "evenements")}>Evènements</NavItem>
							<NavItem eventKey={3} active={this.active("salles")} href="#" onClick={this.navigateTo.bind(this, "salles")}>Salles</NavItem>
							<NavItem eventKey={4} active={this.active("formateurs")} href="#" onClick={this.navigateTo.bind(this, "formateurs")}>Formateurs</NavItem>
						</Nav>
						<Nav navbar right>
							<NavItem eventKey={5} href="#" onClick={this.onLogoff}><Glyphicon glyph="off" /> Déconnexion</NavItem>
						</Nav>
					</CollapsibleNav>
					: null
				}
			</Navbar>
		);
	}
}

Layout.Row = React.createClass({
	getDefaultProps: function () {
		return {
			size: "col-xs-12",
			spacing: ""
		};
	},
	render: function () {
		const classes = {};
		classes[this.props.size] = true;
		classes["spacing" + this.props.spacing] = true;
		
		return (
			<div className={classnames(classes)}>
				{this.props.children}
			</div>
		);
	}
});

Layout.Table = React.createClass({
	propTypes: {
		cols: React.PropTypes.array.isRequired
	},
	render() {
		const headers = this.props.cols.map(col => <th key={col}>{col}</th>);
		headers.push(<th key="nbsp">&nbsp;</th>);
		 
		return (
			<table className="table table-hover">
				<thead>
					<tr>
						{headers}
					</tr>
				</thead>
				<tbody>
					{this.props.children}
				</tbody>
			</table>
		);
	}
});

export const Form = {};

Form.Text = React.createClass({
	mixins: [LinkedStateMixin],
	propTypes: {
		name: React.PropTypes.string.isRequired,
		label: React.PropTypes.string.isRequired,
		type: React.PropTypes.string,
		min: React.PropTypes.number
	},
	getdefaultProps: function () {
		return {
			type: "text"
		};
	},
	render: function () {
		const classes = classnames({
			"form-group": true,
			"col-xs-12": true,
			"col-md-4": this.props.type === "number"
		});
		
		const attrs = {};
		
		if (this.props.min) {
			attrs.min = this.props.min;
		}

		return (
			<div className="row">
				<div className={classes}>
					<label htmlFor={this.props.name}>{this.props.label}</label>
					<input type={this.props.type} id={this.props.name} name={this.props.name} valueLink={this.props.value} required className="form-control" {...attrs} />
				</div>
			</div>
		);
	}
});

Form.Select = React.createClass({
	propTypes: {
		name: React.PropTypes.string.isRequired,
		value: React.PropTypes.object.isRequired,
		label: React.PropTypes.string.isRequired,
		options: React.PropTypes.array.isRequired,
		optionMapper: React.PropTypes.func.isRequired
	},
	render() {
		const options = this.props.options
			.map(this.props.optionMapper)
			.map(option => <option key={option.value} value={option.value}>{option.label}</option>);
			
		options.unshift(<option key="--"></option>);
			
		return (
			<div className="form-group">
				<label htmlFor={this.props.name}>{this.props.label}</label>
				<select id={this.props.name} className="form-control" valueLink={this.props.value}>
					{options}
				</select>
			</div>
		);
	}
});

Form.Checkbox = React.createClass({
	propTypes: {
		label: React.PropTypes.string.isRequired,
		value: React.PropTypes.object.isRequired
	},
	render: function () {
		return (
			<div className="checkbox">
				<label>
					<input type="checkbox" checkedLink={this.props.value} /> {this.props.label}
				</label>
			</div>
		);
	}
});

Form.Submit = React.createClass({
	propTypes: {
		create: React.PropTypes.bool.isRequired
	},
	render: function () {
		const icon = this.props.create ? "plus" : "ok";
		const label = this.props.create ? "Créer" : "Sauvegarder";
		
		return (
			<div className="row">
				<div className="col-xs-12">
					<button type="submit" className="btn btn-primary"><Glyph icon={icon} /> {label}</button>
				</div>
			</div>
		);
	}
});

export const Glyph = ({icon}) => (
	<Glyphicon glyph={icon} />
);

export const Label = (props) => {
	const { primary, success, info, warning, danger, link, children } = props;
	
	const classes = classnames({
		label: true,
		"label-primary": primary,
		"label-success": success,
		"label-info": info,
		"label-warning": warning,
		"label-danger": danger,
		"label-link": link,
		"label-default": !(primary || success || info || warning || danger || link)
	});
	
	return <span className={classes}>{children}</span>;
};

