import moment from "moment";
import React from "react";
import { Layout, Glyph } from "./layout";
import APP from "./app";
import storage from "./storage";
import Evenements from "./evenements";
import Stagiaires from "./stagiaires";
import Panel from "react-bootstrap/lib/Panel";

const Dashboard = React.createClass({
	onClick(view, record) {
		APP.render(view, {
			view: "edit",
			record
		});
	},
	render() {
		const events = storage.get("events");
		events.sort((event1, event2) => {
			if (moment(event1.date).isBefore(event2.date)) {
				return 1;
			} else if (moment(event1.date).isAfter(event2.date)) {
				return -1;
			} else {
				return 0;
			}
		});
		const eventRows = events.map(event => {
			const tableName = event.getTableName();
			const record = storage.get(tableName, event.payload);
			const onClick = APP.edit.bind(APP, tableName, record.id);
			
			return <Event key={event.getKey()} type={event.type} record={record} onClick={onClick} />;
		});
	
		return (
			<div>
				<Layout.Row>
					<h1>Dashboard</h1>
				</Layout.Row>
				<Layout.Row spacing="Top">
					<button onClick={this.onClick.bind(this, "stagiaires", Stagiaires.create())} type="button" className="btn btn-default btn-lg"><Glyph icon="user" /> Créer stagiaire</button>
					{" "}
					<button onClick={this.onClick.bind(this, "evenements", Evenements.create())} type="button" className="btn btn-default btn-lg"><Glyph icon="calendar" /> Créer évènement</button>
				</Layout.Row>
				<Layout.Row spacing="Top">
					{eventRows}
				</Layout.Row>
			</div>
		);
	}
});

const Event = ({type, record, onClick}) => {
	let text;
	
	switch (type) {
		case "stagiaires-create":
			text = `Le stagiaire ${record.prenom} ${record.nom} a été créé`;
			break;
		case "evenements-create":
			text = `L'évènement ${record.nom} a été programmé pour le ${moment(record.date).format("DD/MM/YYYY")}`;
			break;
		case "salles-create":
			text = `La salle ${record.nom} a été créée`;
			break;
		case "formateurs-create":
			text = `Le formateur ${record.nom} a été créé`;
			break;
		default:
			return <div>UNKNOWN EVENT: {type}</div>
	}
	
	return (
		<Panel onClick={onClick} className="spacingTop clickable">
			{text}
		</Panel>
	);
};

export default Dashboard;



